<template>
    <yd-layout>
        <div class="otherAttend">
            <div class="search" :class="{active: searchFlag}">
                <input class="searchWord" type="text" v-model="inputName" @focus="searchFlag = true" placeholder="戳我搜员工姓名哦">
                <span @click="searchFlag = false;inputName='';searchList=[]" class="cancel" v-show="searchFlag">取消</span>
                <div @click="inputName='';" class="close" v-show="searchFlag">
                    <img src="../../../static/images/workCenter/cancel.png" alt="">
                </div>
                </div>
                <div class="addPeople" v-show="searchFlag==true">
                    <yd-flexbox v-for="(item, index) in searchList" :key="index">
                        <div class="addName" v-text="item.empname"></div>
                        <div class="addDepart" :class="{lineACtive:item.check===true}" v-text="splitArray(item.jobTitles)" @click="getPerson(searchList,index);textJob(splitArray(item.jobTitles))"></div>
                    </yd-flexbox>
                </div>
                <div v-show="searchFlag == false">
                    <div class="schedule2" v-show="departList.length!=0">
                        <div class="swiper-container">
                            <div class="swiper-wrapper">
                                <div class="swiper-slide" :class="{'departActive':swiperFlag==item.orgname}" @click="swiperFlag=item.orgname;getorgAllUser(item.ORGID)" v-for="(item, index) in departList" :key="index" v-text="item.orgname"></div>
                            </div>
                        </div>
                    </div>
                    <div class="addPeople">
                        <yd-flexbox v-for="(item, index) in userList" :key="index">
                            <div class="addName" v-text="item.empname"></div>
                            <div class="addDepart" :class="{lineACtive:item.check===true}" v-text="splitArray(item.jobTitles)" @click="getPerson(userList,index);textJob(splitArray(item.jobTitles))"></div>
                        </yd-flexbox>
                    </div>
                </div>
            </div>
            <yd-popup v-model="jobShow" position="center" width="5.79rem">
                <div class="jobClass">{{jobText}}</div>
            </yd-popup>
            <yd-tabbar slot="tabbar">
                <div class="agree" @click="$router.go(-1)">确定</div>
            </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'personOrg',
    data() {
        return {
            swiperFlag: '',
            departList: [],
            userList: [],
            contactList: [],
            show: false,
            userid: '',
            empname: '',
            deList: [],
            departNum: 0,
            inputName: '',
            searchList: [],
            searchFlag: false,
            numIndex: '',
            jobShow: false,
            jobText: '',
            selectPerson: [],
            selEmpCode: [],
            selUserId: [],
            selUserName: []
        }
    },
    props: {
        moreType: {
            type: Boolean,
            default: true
        },
        onlyType: {
            type: String,
            default: 'empcode'
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('');
        vm.selEmpCode = vm.searchInfo.selEmpCode;
        vm.selUserId = vm.searchInfo.selUserId;
        vm.selectPerson = vm.searchInfo.selectPerson;
        vm.selUserName = vm.searchInfo.selUserName;
        vm.getDeptAllOrg();
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            searchInfo: 'getSearchInfo'
        })
    },
    watch: {
        inputName: function() {
            this.search();
        },
        selectPerson: function() {
            let vm = this;
            vm.setSearchInfo({
                selectPerson: vm.selectPerson
            })
        },
        selUserId: function() {
            let vm = this;
            vm.setSearchInfo({
                selUserId: vm.selUserId
            })
            if (vm.onlyType == 'userId') {
                vm.userList.map((item) => {
                    if (vm.selUserId.includes(item.userId)) {
                        item.check = true;
                    } else {
                        item.check = false;
                    }
                });
            }
        },
        selUserName: function() {
            let vm = this;
            vm.setSearchInfo({
                selUserName: vm.selUserName
            })
        },
        selEmpCode: function() {
            let vm = this;
            vm.setSearchInfo({
                selEmpCode: vm.selEmpCode
            });
            if (vm.onlyType == 'empcode') {
                vm.userList.map((item) => {
                    if (vm.selEmpCode.includes(item.empcode)) {
                        item.check = true;
                    } else {
                        item.check = false;
                    }
                });
            }
        }
    },
    methods: {
        ...mapMutations([
            'setSearchInfo'
        ]),
        getPerson(arr, index) {
            let vm = this;
            if (arr[index].check) {
                arr[index].check = false;
                if (vm.onlyType == 'empcode') {
                    for (let i = 0; i < vm.selEmpCode.length; i++) {
                        let item = vm.selEmpCode[i];
                        if (item === arr[index].empcode) {
                            vm.selEmpCode.splice(i, 1);
                            vm.selUserId.splice(i, 1);
                            vm.selUserName.splice(i, 1);
                        }
                    }
                } else if (vm.onlyType == 'userId') {
                    for (let i = 0; i < vm.selUserId.length; i++) {
                        let item = vm.selUserId[i];
                        if (item === arr[index].userId) {
                            vm.selEmpCode.splice(i, 1);
                            vm.selUserId.splice(i, 1);
                            vm.selUserName.splice(i, 1);
                        }
                    }
                }
            } else {
                if (vm.moreType && vm.selEmpCode.length == 1) {
                    vm.$dialog.toast({
                        mes: '只能选择一个人',
                        timeout: 2000
                    });
                } else {
                    if (arr[index].empcode == null) {
                        vm.$dialog.toast({
                            mes: '此人没有工号,不能添加。',
                            timeout: 2000
                        });
                    } else {
                        vm.selEmpCode.push(arr[index].empcode);
                        vm.selUserId.push(arr[index].userId);
                        vm.selUserName.push(arr[index].empname);
                        arr[index].check = true;
                    }
                }
            }
            vm.$forceUpdate();
        },
        // 查询科室
        getDeptAllOrg() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/getDeptAllOrg', {
                'name': vm.userInfo.ssotoken
            }, function(res) {
                vm.departList = res.jsonData;
                let itemArr = '';
                for (let i = 0; i < vm.departList.length; i++) {
                    if (vm.departList[i].orgname == '总经理室') {
                        vm.deList.unshift(vm.departList[i].orgname);
                        itemArr = vm.departList[i];
                        vm.departList.splice(i, 1);
                    }
                    vm.deList.push(vm.departList[i].orgname);
                }
                vm.departList.unshift(itemArr);
                vm.thisDepart();
            });
        },
        // 查询科室人员
        getorgAllUser(orgId) {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/portal/getOrgByOrgId', {
                'orgId': orgId,
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function(res) {
                vm.userList = res.jsonData;
                vm.userList.map((item) => {
                    if ((vm.selEmpCode.includes(item.empcode) && vm.onlyType == 'empcode') || (vm.selUserId.includes(item.userId) && vm.onlyType == 'userId')) {
                        item.check = true;
                    } else {
                        item.check = false;
                    }
                });
            });
        },
        // 显示本部门的成员
        thisDepart() {
            let vm = this;
            var promise = new Promise(function(resolve, reject) {
                vm.get(vm.conf.portalMobile + '/portal/getOrgByOrgId', {
                    'orgId': vm.userInfo.orgId,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
                }, function(res) {
                    vm.userList = res.jsonData;
                    vm.userList.map((item) => {
                        if ((vm.selEmpCode.includes(item.empcode) && vm.onlyType == 'empcode') || (vm.selUserId.includes(item.userId) && vm.onlyType == 'userId')) {
                            item.check = true;
                        } else {
                            item.check = false;
                        }
                    });
                    if (vm.userList[0].jobTitles.split('/')[0] != '公司领导') {
                        vm.swiperFlag = vm.userList[0].jobTitles.split('/')[1];
                    }
                    vm.departNum = vm.deList.indexOf(vm.swiperFlag);
                    resolve(true);
                });
            });
            promise.then(function(data) {
                if (data) {
                    vm.loadSwiper();
                }
            });
        },
        // swiper插件
        loadSwiper() {
            let vm = this;
            vm.$nextTick(() => {
                this.galleryTop = new Swiper('.swiper-container', {
                    initialSlide: vm.departNum,
                    slidesPerView: 'auto',
                    paginationClickable: true,
                    observer: true, // 修改swiper自己或子元素时，自动初始化swiper
                    observeParents: true // 修改swiper的父元素时，自动初始化swiper
                })
            });
        },
        splitArray(jobTitles) {
            let vm = this;
            let arr = [];
            arr = jobTitles.split('/');
            for (let i = 0; i < arr.length; i++) {
                if (vm.swiperFlag == arr[i]) {
                    arr.splice(i, 1);
                }
            }
            return arr.join('/');
        },
        // 搜索接口
        search() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/portal/getUsersByName', {
                'userName': vm.trim(vm.inputName),
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function(res) {
                if (res.jsonData == '') {
                    vm.searchList = [];
                } else {
                    vm.searchList = res.jsonData;
                }
                vm.searchList.map((item) => {
                    if (vm.selEmpCode.includes(item.empcode)) {
                        item.check = true;
                    } else {
                        item.check = false;
                    }
                });
            });
        },
        textJob(job) {
            let vm = this;
            if (job.length > 17) {
                vm.jobShow = true;
                vm.jobText = job;
            }
        }
    }
}
</script>
<style lang="less" scoped>
// 底部按钮
.agree {
    flex: 1;
    color: #fff;
    font-size: .36rem;
    line-height: 1rem;
    text-align: center;
}

.agree {
    background-color: #59a5fd;
}

.otherAttend {
    height: 100%;
    background-color: #f8f8f8;
}

.searchBox {
    width: 100%;
    height: 1.16rem;
    background-color: #fff;
    padding-top: .22rem;

    .searchContent {
        height: .72rem;
        background-color: #f0f0f0;
        margin-left: .43rem;
        margin-right: .28rem;
        border-radius: 5px;

        .searchImg {
            width: .4rem;
            height: .4rem;
            margin-top: .16rem;
            margin-left: .27rem;
            float: left;
        }

        .searchInput {
            font-size: inherit;
            display: block;
            border: none;
            float: left;
            height: .72rem;
            padding-left: .38rem;
            font-size: .28rem;
            color: #999;
            width: 5.7rem;
        }

        .searchInput::-webkit-input-placeholder {
            color: #999;
        }
    }
}

.mainContentFluid {
    margin-top: 0;
}

.setTop {
    margin-top: 0.2rem;
    height: .93rem;
    background-color: #f0f0f0;
    padding-left: .43rem;

    .yd-flexbox {
        line-height: .93rem;
        font-size: .3rem;
        color: #999;

        .depart1 {
            width: 2.11rem;
        }

        .depart2 {
            width: 2.25rem
        }

        .depart3 {
            width: 1.67rem
        }

        .active {
            color: #333;
        }
    }
}

.addPeople {
    background-color: #fff;

    .yd-flexbox {
        min-height: 1.18rem;
        color: #333;
        font-size: .3rem;
        padding-left: .43rem;
        border-bottom: 1px solid #efefef;

        .addName {
            width: 1.9rem;
            // line-height: 1.18rem;
        }

        .addImg1 {
            width: .4rem;
            height: .3rem;
        }

        .addImg2 {
            width: .48rem;
            height: .48rem;
            margin-left: .3rem;
        }

        .addDepart {
            width: 100%;
            line-height: 1.18rem;
            height: 1.18rem;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            margin-right: .4rem;
            padding-right: .5rem;
        }
    }
}

.name_circle ul li {
    width: 16.66%;
    margin-bottom: .2rem;
}

.swiper-container {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    text-align: left;
    font-size: .3rem;
    width: 25%;
    color: #999999;
    line-height: .93rem;
}

.schedule2 {
    height: .93rem;
    padding-left: .43rem;
    // margin-top: .2rem;
    position: relative;
    background-color: #f0f0f0;
}

.swiper-slide {
    margin-right: .28rem;
    height: .93rem;
}

.departActive {
    color: #333;
}

.other {
    border-bottom: none;
}

.jobClass {
    min-height: 0.93rem;
    line-height: 0.93rem;
    text-align: center;
    font-size: 0.3rem;
    border-bottom: 1px solid #e3e3e3;
}

.search {
    background: #fff;
    padding: .3rem;
    position: relative;

    &.active {
        padding: .3rem;

        .searchWord {
            width: 88%;
        }
    }

    .cancel {
        color: #59a5fd;
        font-size: .3rem;
        position: absolute;
        right: .26rem;
        top: .3rem;
        height: .7rem;
        line-height: .7rem;
        text-align: center;
    }

    .close {
        width: .66rem;
        height: .7rem;
        text-align: center;
        position: absolute;
        top: .3rem;
        right: 1.2rem;
        line-height: .65rem;

        img {
            width: .38rem;
            vertical-align: middle;
        }
    }

    .searchWord {
        background-color: #f3f3f3;
        background-position: .27rem;
        border-radius: .08rem;
        text-align: left;
        padding-left: .66rem;
        padding-right: .6rem;
    }
}

.searchWord {
    width: 100%;
    height: .7rem;
    padding-top: .17rem;
    padding-bottom: .22rem;
    border: 0;
    box-sizing: border-box;
    text-align: center;
    color: #2c3e50;
    outline: none;
    border-radius: 2px;
    margin-right: 10px;
    transition: border-color 0.2s ease;
    background: #fff url(../../../static/images/schedule/s_3.png) 47% center no-repeat;
    background-size: .26rem;
    padding-left: .56rem;
    font-size: .28rem;
    vertical-align: middle !important;
}

.lineACtive {
    background: url(../../../static/images/trs_up_03.png) no-repeat;
    background-size: .4rem .3rem;
    background-position: right center;
}
</style>


